export default {
  props: {
    routeName: String,
    // 父组件要传activeColor值
    activeColor: {
      type: String,
      default: '#A52C2B'
    }
  },
  computed: {
    isActive() {
      return this.$route.name.indexOf(this.routeName) !== -1;
    },

    activeStyle() {
      return this.isActive ? {
        color: this.activeColor
      } : {};
    }

  }
};